<template>
  <div class="guide">
    <van-swipe class="guide-group" :loop="false">
      <van-swipe-item class="guide-item">
        <img class="guide-item-img" :src="guide1">
        <p class="guide-desc">沟通</p>
        <p>随时随地</p>
      </van-swipe-item>
      <van-swipe-item class="guide-item">
        <img class="guide-item-img" :src="guide2">
        <p class="guide-desc">协作</p>
        <p>无处不在</p>
      </van-swipe-item>
      <van-swipe-item class="guide-item">
        <img class="guide-item-img" :src="guide3">
        <p class="guide-desc">C-me会议 全新体验</p>
        <van-button class="guide-btn" @click="toIndexPage">开始使用</van-button>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>
<script>
import { Swipe, SwipeItem, Button } from 'vant'
import {setFirstFlag} from '@/utils/tools'
import guide1 from '@/assets/img/guide1.png'
import guide2 from '@/assets/img/guide2.png'
import guide3 from '@/assets/img/guide3.png'

export default {
  components: {
    [Swipe.name]: Swipe,
    [Button.name]: Button,
    [SwipeItem.name]: SwipeItem
  },
  data () {
    return {
      guide1,
      guide2,
      guide3
    }
  },
  methods: {
    toIndexPage () {
      setFirstFlag()
      this.$router.push({
        path: '/manage/index',
        query: {}
      })
    }
  },
  created () {

  }
}
</script>
<style scoped>
.guide{
  font-size: 14px;
  color: #666;
}
.guide-group{
  height: 100vh;
}
.guide-item{
  text-align: center;
}
.guide-item-img{
  height: 450px;
}
.guide-desc{
  padding: 30px 0;
}
.guide-btn{
  color: #0F8EE6;
  border-color: #B5CBD2;
}
</style>



